import _tool from '@/static/zaiui/util/tools.js';
export default {
	name: 'sell-quickly-list',
	data() {
		return {
			ListData: { swiper: [], list: [] }, indexs: 0,
		};
	},
	props: {
		list_data: {
			type: Object,
			default: () => {
				return {}
			}
		}
	},
	created() {
		this.ListData = this.list_data;
	},
	watch: {
		list_data() {
			this.ListData = this.list_data;
		}
	},
	methods: {
		earnSwiper(e) {
			this.indexs = e.detail.current;
		},
		listTap(data,index) {
			this.$emit('listTap', {
				data,
				index
			});
		}
	}
}
